"use client";

import { useState } from "react";
import RegulatedInfrastructure from "./WhyManjam/RegulatedInfrastructure";
import ShariahCompliant from "./WhyManjam/ShariahCompliant";
import InstitutionalSecurity from "./WhyManjam/InstitutionalSecurity";
import HybridOperatingModel from "./WhyManjam/HybridOperatingModel";

const tabs = [
  {
    id: "regulated",
    label: "Regulated Infrastructure",
    component: RegulatedInfrastructure,
  },
  {
    id: "sharia",
    label: "Sharia-Compliant",
    component: ShariahCompliant,
  },
  {
    id: "security",
    label: "Institutional-Grade Security & Compliance",
    component: InstitutionalSecurity,
  },
  {
    id: "hybrid",
    label: "Hybrid Operating Model",
    component: HybridOperatingModel,
  },
];

export default function WhyManjam() {
  const [activeTab, setActiveTab] = useState("regulated");

  const activeTabData = tabs.find((tab) => tab.id === activeTab);
  const ActiveComponent = activeTabData?.component;

  return (
    <section className="w-full py-20">
      <div className="flex flex-col lg:flex-row gap-16">
        {/* Left Side */}
        <div className="lg:w-[45%]">
          {/* Label */}
          <p className="text-[#10B981] font-semibold text-sm tracking-wider uppercase mb-4">
            WHY MANJAM?
          </p>

          {/* Heading */}
          <h2 className="text-[#111827] text-4xl lg:text-[44px] font-semibold leading-tight mb-12">
            Designed for safety, clarity, and long-term growth.
          </h2>

          {/* Tabs */}
          <div className="relative">
            {/* Vertical line */}
            <div className="absolute left-0 top-0 bottom-0 w-[2px] bg-[#E5E7EB]"></div>

            <div className="flex flex-col">
              {tabs.map((tab) => (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id)}
                  className={`relative pl-6 py-3 text-left transition-all ${
                    activeTab === tab.id
                      ? "text-[#111827] font-semibold"
                      : "text-[#9CA3AF] font-normal hover:text-[#6B7280]"
                  }`}
                >
                  {/* Active indicator */}
                  {activeTab === tab.id && (
                    <div className="absolute left-0 top-1/2 -translate-y-1/2 w-[2px] h-6 bg-[#10B981]"></div>
                  )}
                  {activeTab === tab.id && (
                    <div className="absolute left-[-3px] top-1/2 -translate-y-1/2 w-2 h-2 bg-[#10B981] rounded-full"></div>
                  )}
                  {tab.label}
                </button>
              ))}
            </div>
          </div>
        </div>

        {/* Right Side - Feature Card */}
        <div className="lg:w-[55%]">
          {ActiveComponent && <ActiveComponent />}
        </div>
      </div>
    </section>
  );
}
